import { debounce } from 'utilities/object';

export default {
	data() {
		return this.$vnode.data
	},

	computed: {
		editColor() {
			if ( ! this.color )
				return '';

			return this.color;
		}
	},

	created() {
		this.updateName = debounce(this.updateName, 250);
		this.updateColor = debounce(this.updateColor, 250);
	},

	updated() {
		this.updateTip();
	},

	methods: {
		setOriginalName() {
			this.name = this.originalName;
			this.setName(this.name);
		},

		setOriginalColor() {
			this.color = this.originalColor;
			this.setColor(this.color);
		},

		clearName() {
			this.name = null;
			this.deleteName();
		},

		updateName() {
			const value = this.$refs.name.value;
			if ( value == null || ! value.length ) {
				this.clearName();
				return;
			}

			this.name = value;
			this.setName(value);
		},

		clearColor() {
			this.color = null;
			this.deleteColor();
		},

		updateColor(value) {
			if ( value == null || ! value.length ) {
				this.clearColor();
				return;
			}

			this.color = value;
			this.setColor(value);
		}
	}
}

